import PostClient from "@/components/post/PostClient";
import { createClient } from "@/utils/supabase/server";
import { redirect } from "next/navigation";
import GeminiProductFeed, {getGeminiProductFeed,} from "@/components/feed/GeminiProductFeed";

// üí° GeminProductFeed.tsxÏóêÏÑú FeedItem ÌÉÄÏûÖÏùÑ export Ìï¥Ïïº Ìï©ÎãàÎã§.
// (ÎòêÎäî types/index.d.tsÎ°ú ÏòÆÍ∏∞ÏÑ∏Ïöî)
import { FeedItem } from "@/components/feed/GeminiProductFeed";
import FeedPostClient from "@/components/feed/FeedPostClient";

export default async function PostDetailPage({
  params,
}: {
  params: Promise<{ id: string }>;
}) {
  const { id } = await params;
  const supabase = await createClient();
  const {
    data: { user },
  } = await supabase.auth.getUser();
  const { data: postRows, error: postError } = await supabase
    .from("posts")
    .select(
      `
    id,
    title,
    content,
    tags,
    created_at,
    profile_id,
    profiles(*),
    comments(*)
    `
    )
    .eq("id", parseInt(id))
    .single();

  if (postError || !postRows) {
    redirect("/");
  }
  return (
    <>
      <PostClient postRows={postRows} userId={user?.id} />
    </>
  );
}

export async function FeedItemPage({
  params,
}: {
  params: { id: string };
}) {
  const { id: encodedGuid } = params;

  if (!encodedGuid) {
    redirect("/");
  }

  // 1. Base64Î°ú Ïù∏ÏΩîÎî©Îêú guidÎ•º ÎîîÏΩîÎî©
  // atob: Base64 Î¨∏ÏûêÏó¥ÏùÑ ÏõêÎûò guidÎ°ú ÎîîÏΩîÎî©
  const guid = atob(encodedGuid);

  // 2. RSS ÌîºÎìúÎ•º Îã§Ïãú Í∞ÄÏ†∏Ïò¥ (Next.js fetch Ï∫êÏãúÍ∞Ä Ïû¨ÏÇ¨Ïö©)
  const items = await getGeminiProductFeed();

  // 3. guidÎ°ú Ìï¥Îãπ ÏïÑÏù¥ÌÖú Ï∞æÍ∏∞
  const item = items.find((i: FeedItem) => i.guid === guid);

  if (!item) {
    redirect("/");
  }

  // 4. Ï∞æÏùÄ itemÏùÑ ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ Ïª¥Ìè¨ÎÑåÌä∏Ïóê Ï†ÑÎã¨
  return <FeedPostClient item={item} />;
}